import {UIElement} from "./UIElement";
import {Layout} from "../Customizations/Layout";
import Translations from "./i18n/Translations";
import {FixedUiElement} from "./Base/FixedUiElement";
import Combine from "./Base/Combine";
import {VariableUiElement} from "./Base/VariableUIElement";
import {UIEventSource} from "./UIEventSource";

export class ShareScreen extends UIElement {

    private _shareButton: UIElement;

    private _options: UIElement;
    private _iframeCode: UIElement;

    constructor(layout: Layout) {
        super(undefined)
        const tr = Translations.t.general.sharescreen;

        this._shareButton = tr.shareButton.Clone().onClick(() => {
            console.log("Clicked")
            const shareData = {
                title: 'MDN',
                text: 'Learn web development on MDN!',
                url: 'https://developer.mozilla.org',
            }
            try {
                navigator.share(shareData).then(() => {
                    // share ok
                    console.log("Shared!")
                }, () => {
                    console.log("Rejected")
                    // rejected: copy to clipboard
                })
            } catch (err) {
                console.log("Error")
            }

        })

        const options = new UIEventSource({
            'fs-search': false
        })



      const url=  options.map((options) => {

                let literalText = "https://pietervdvn.github.io/MapComplete/"+layout.name+".html"
                return literalText;
            }
        );
        this._iframeCode = new VariableUiElement(
           url.map((url) => 
           {
               return `<span class='literal-code iframe-code-block'>${url}</span>`
           })
        );


    }

    InnerRender(): string {

        const tr = Translations.t.general.sharescreen;

        return new Combine([
            tr.intro,
            this._shareButton,
            tr.embedIntro,
            this._iframeCode
        ]).Render()
    }

}